@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700");
@lost flexbox flex;
@lost gutter 20px;
html, body {
  padding: 0;
  margin: 0;
  height: 100vh;
}

body {
  font-size: responsive 16px 20px;
  font-family: 'Roboto', sans-serif;
}

section {
  background-color: rgba(255, 0, 0, 0.2);
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 4px solid #f00;
  padding: 0;
}

.item {
  text-align: center;
  margin-bottom: 0 !important;
  transition: all 0.3s ease;
  width: calc(100% / 4 - 20px);
  background-color: #fff;
  border: 1px solid #fff;
}

@media (max-width: 768px) {
  .item {
    width: calc(100% / 2 - 20px);
  }
}

.item:hover {
  background-color: #fff;
  border: 1px solid #fff;
  transform: scale(1.07);
  box-shadow: 0 25px 15px -20px rgba(0, 0, 0, 0.7);
}

.grid-container {
  lost-center: 1140px 4pr;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.grid-item {
  height: 1000vh;
  lost-column: 1/4;
  background-color: rgba(238, 238, 238, 0.1);
  padding: 10pr;
  box-sizing: border-box;
  margin-bottom: 20pr;
}

@media (max-width: 1280px) {
  .grid-item {
    lost-column: 1/3;
  }
}

@media (max-width: 768px) {
  .grid-item {
    lost-column: 1/2;
  }
}

@media (max-width: 465px) {
  .grid-item {
    lost-column: 1/1;
  }
}

.grid-item img {
  display: block;
  width: 100%;
  height: 200pr;
  object-fit: cover;
  object-position: center;
  border-radius: 10px;
}

.container {
  background-color: #212121;
  color: #f1f1f1;
  border-bottom: 4px solid #f00;
  box-shadow: 0 4px 15px 4px rgba(0, 0, 0, 0.3);
  z-index: 100;
  position: relative;
  padding: 0;
  margin: 0;
}

.container h1 {
  lost-center: 1140px 10pr;
  padding: 40pr 10pr;
  margin: 0 auto 20pr;
  font-size: responsive 20pr 40pr;
  font-weight: 100;
  line-height: 1.4;
  text-align: center;
  box-sizing: border-box;
}

.img-container .section-img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center 90%;
  height: 73.5vh;
}
