.container {
    background-color: #212121;
    color: #f1f1f1;
    border-bottom: 4px solid #f00;
    box-shadow: 0 4px 15px 4px rgba(#000, 0.3);
    z-index: 100;
    position: relative;
    padding: 0;
    margin: 0;

    h1 {
        lost-center: 1140px 10pr;
        padding: 40pr 10pr;
        margin: 0 auto 20pr;
        font-size: responsive 20pr 40pr;
        font-weight: 100;
        line-height: 1.4;
        text-align: center;
        box-sizing: border-box; } }
.img-container {

    .section-img {
        width: 100%;
        height: auto;
        object-fit: cover;
        object-position: center 90%;
        height: 73.5vh; } }
